.png"),"alt":"Texto que describa la imagen"}})]),_c('p',{staticClass:"text-bold text-center"},[_vm._v("Metabisulfito de sodio")]),_c('p',{staticClass:"mb-0 text-center"},[_vm._v("Este producto es utilizado para controlar el pH del material ensilado. Se añade de 3 a 4 kg por tonelada de pasto y no debe excederse al agregar este compuesto porque puede generar que el animal rechace el forraje ensilado.")])]),_c('div',{staticClass:"tarjeta bgr_9 p-4"},[_c('figure',{staticClass:"mb-3 d-flex justify-content-center"},[_c('img',{staticClass:"pst_4",attrs:{"src":require("@/assets/curso/temas/tema4/10.png"),"alt":"Texto que describa la imagen"}})]),_c('p',{staticClass:"text-bold text-center"},[_vm._v("Inoculantes microbiales")]),_c('p',{staticClass:"mb-0 text-center"},[_vm._v("Las bacterias que se adicionan al material a ensilar ayudan a dominar la fermentación de los cultivos microbiales en el silo. Las categorías más usadas son las homofermentadoras que solo producen ácido láctico ("),_c('em',[_vm._v("Lactobacillus plantarum, Enterococcus, Pediococcus spp")]),_vm._v(").")])])])],1)]),_vm._m(6),_vm._m(7),_c('div',{staticClass:"bg_img"},[_c('p',[_vm._v("Las fases de un ensilaje son los procesos mediante los cuales se conserva forraje en un ambiente anaeróbico. Sirven para preservar los nutrientes del forraje, asegurando alimento de calidad en épocas de escasez. Funcionan en cuatro etapas: fase aeróbica, fase de fermentación, fase estable y fase de deterioro, donde los microorganismos ayudan a conservar el alimento adecuadamente. A continuación, se explica las funciones de cada fase:")]),_c('div',{staticClass:"row mb-5 justify-content-center"},[_vm._m(8),_c('div',{staticClass:"col-lg-8 align-content-center"},[_c('LineaTiempoD',{staticClass:"color-secundario"},[_c('p',{staticClass:"text-small",attrs:{"numero":"1","titulo":"Fase aeróbica"}},[_vm._v("En esta fase que dura sólo pocas horas, el oxígeno atmosférico presente en la masa vegetal disminuye rápidamente debido a la respiración de los materiales vegetales y a los microorganismos aeróbicos y aeróbicos facultativos como las levaduras y las enterobacterias. Además, hay una actividad importante de varias enzimas vegetales, como las proteasas y las carbohidrasas, siempre que el pH se mantenga en el rango normal para el jugo del forraje fresco (pH 6,5 - 6,0).")]),_c('p',{staticClass:"text-small",attrs:{"numero":"2","titulo":"Fase de fermentación"}},[_vm._v("Esta fase comienza al producirse un ambiente anaeróbico. Dura de varios días hasta varias semanas, dependiendo de las características del material ensilado y de las condiciones en el momento del ensilaje. Si la fermentación se desarrolla con éxito, la actividad BAC proliferará y se convertirá en la población predominante. A causa de la producción de ácido láctico y otros ácidos, el pH bajará a valores entre 3,8 a 5,0.")]),_c('p',{staticClass:"text-small",attrs:{"numero":"3","titulo":"Fase estable"}},[_vm._v("Mientras se mantenga el ambiente sin aire, ocurren pocos cambios. La mayoría de los microorganismos de la Fase 2 lentamente reducen su presencia. Algunos microorganismos acidófilos sobreviven este periodo en estado inactivo; otros, como clostridios y bacilos, sobreviven como esporas. Sólo algunas proteasas y carbohidrasas, y microorganismos especializados, como Lactobacillus buchneri que toleran ambientes ácidos, continúan activos, pero a menor ritmo.")]),_c('p',{staticClass:"text-small",attrs:{"numero":"4","titulo":"Fase de deterioro aeróbico"}},[_vm._v("Esta fase comienza con la apertura del silo y la exposición del ensilaje al aire. Esto es inevitable cuando se requiere extraer y distribuir el ensilaje, pero puede ocurrir antes de iniciar la explotación por daño de la cobertura del silo (p. ej. roedores o pájaros). El periodo de deterioro puede dividirse en dos etapas. La primera se debe al inicio de la degradación de los ácidos orgánicos que conservan el ensilaje, por acción de levaduras y ocasionalmente por bacterias que producen ácido acético. Esto induce un aumento en el valor del pH, lo que permite el inicio de la segunda etapa de deterioro; en ella se constata un aumento de la temperatura y la actividad de microorganismos que deterioran el ensilaje, como algunos bacilos. La última etapa también incluye la actividad de otros microorganismos aeróbicos -también facultativos- como mohos y enterobacterias. El deterioro aeróbico ocurre en casi todos los ensilajes al ser abiertos y expuestos al aire. Sin embargo, la tasa de deterioro depende de la concentración y de la actividad de los organismos que causan este deterioro en el ensilaje. (Oude, Driehuis, Gottschal y Spoelstra, s.f.) ")])])],1)])]),_vm._m(9),_vm._m(10),_vm._m(11),_vm._m(12),_c('SlyderF',{attrs:{"columnas":"col-md-6 col-xl-3"}},[_c('div',{staticClass:"tarjeta--boton bgr_5 p-4 h-100"},[_c('div',{staticClass:"row justify-content-center mb-3"},[_c('div',{staticClass:"col-7"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema4/14.svg"),"alt":"Texto que describa la imagen"}})])])]),_c('h3',{staticClass:"text-center"},[_vm._v("Forraje")]),_c('p',{staticClass:"text-small text-center"},[_vm._v("El forraje para ensilar debe tener el tiempo de cosecha requerido para que esté en el punto óptimo de nutrientes.")])]),_c('div',{staticClass:"tarjeta--boton bgr_6 p-4 h-100"},[_c('div',{staticClass:"row justify-content-center mb-3"},[_c('div',{staticClass:"col-7"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema4/15.svg"),"alt":"Texto que describa la imagen"}})])])]),_c('h3',{staticClass:"text-center"},[_vm._v("Material")]),_c('p',{staticClass:"text-small text-center"},[_vm._v("Picar el material en trozos bien pequeños de 1 a 3 cms.")])]),_c('div',{staticClass:"tarjeta--boton bgr_5 p-4 h-100"},[_c('div',{staticClass:"row justify-content-center mb-3"},[_c('div',{staticClass:"col-7"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema4/16.svg"),"alt":"Texto que describa la imagen"}}